<template>
  <v-app dark>
    <v-app-bar
      app
      height="100"
      color="light-blue darken-1"
      dark
      class="display-2"
    >
      <span class="font-weight-light">COVID-19</span>
      <v-spacer></v-spacer>
      <span right>vaccinationdata</span>
      <span
      class="body-1">version 0.5</span>
    </v-app-bar>
    <v-main>
      <v-container class="my-3">
        <v-row row wrap>
          <v-col cols="4" >
            <v-sheet 
            color="white"
            elevation="5"
            class="ma-3"
            rounded
            >
              <apexchart
              type="bar"
              :options="OptionsvaccinationOverall"
              :series="SeriesvaccinationOverall"
              >
              </apexchart>

            </v-sheet>
            <v-sheet
            color="white"
            elevation="5"
            class="ma-3"
            rounded
            >
              <apexchart
              type="bar"
              :options="OptionsVaccinationProducer"
              :series="SeriesVaccinationProducer"
              >
              </apexchart>
            </v-sheet>
          </v-col>
          <v-col cols="8">
            <v-sheet
            color="white"
            elevation="5"
            class="ma-3"
            rounded
            >
              <apexchart
              type="area"
              :options="OptionsVaccinationComparison"
              :series="SeriesVaccinationComparison"
              >
              </apexchart>
            </v-sheet>
          </v-col>
        </v-row>
        <v-row>
        </v-row>
      </v-container>
      <v-container
      class="my-3"
      >
        <v-row
        row wrap
        >
          <v-col cols="12">
            <v-sheet
            color="white"
            elevation="5"
            class="ma-3"
            rounded>
            <apexchart
              type="area"
              :options="OptionsNewCasesNewVaccinations"
              :series="SeriesNewCasesNewVaccinations"
              >
            </apexchart>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
      <v-card
        color="light-blue darken-1"
        >
        <div
        class="text-center">
        <v-btn
        class="mr-3"
        href="https://github.com/totaku-alt/totaku-alt.github.io"
        target="_blank"
        icon>
          <v-icon
          color="#01579B"
          large
          >mdi-github</v-icon>
        </v-btn>
        <v-btn
        href="https://twitter.com/diesertortex"
        target="_blank"
        icon>
        <v-icon
        color="#01579B"
        class="ml-3"
        center
        large
        >mdi-twitter</v-icon>
        </v-btn>
        </div>
      </v-card>
  </v-app>
