<script>
import axios from 'axios'


export default {
  name: 'App',
  data () {
    return {
      dataTableStates: null,
      dataTest: null,
      biontechData: null,
      modernaData: null,
      historyDates: [],
      allDates: [],
      sortedDates: [],
      sortedVccinated: [],
      sortedSecondVccinated: [],
      historyNewCases: [],
    }
  },
  components: {
  },
  computed: {
    // Series and Options for overall vaccinations chart
    SeriesvaccinationOverall() {
      return [{
        name: 'Impfungen',
        data: [this.dataTableStates, this.dataTest],
      }]
    },
    OptionsvaccinationOverall(){
      return {
          chart: {
            toolbar: {
              show: false,
            },
          },
          xaxis: {
            categories: ['Nur erste Impfung', '1 & 2 Impfung'],
          },
          yaxis: {
            show: false
          },
          grid: {
            show: false
          },
        }
    },
    //Series and Options for all vaccination producers chart
    SeriesVaccinationProducer() {
      return [{
        data: [this.biontechData, this. modernaData]
      }]
    },
    OptionsVaccinationProducer() {
      return {
        chart: {
          toolbar: {
            show: false
          }
        },
        xaxis: {
          categories: ['Biontech', 'Moderna']
        },
        yaxis: {
          show: false
        },
        grid: {
          show: false
        }
      }
    },
    // Series and Options for 1&2 vaccination-tracking-chart everyday
    SeriesVaccinationComparison() {
      return [{
        name: 'Erste Impfung',
        data: this.sortedVccinated 
      },
      { name: 'Zweite Impfung',
        data: this.sortedSecondVccinated }]
    },
    OptionsVaccinationComparison() {
    return {
        chart: {
            type: 'area',
            zoom: {
                enabled: false
            },
            toolbar: {
                show: false
            }
        },
        title: {
              text: 'Impfungen nach Tagen',
              align: 'center',
              margin: 10,
            },
        dataLabels: {
            enabled: false,
        },
        stroke: {
            curve: 'smooth'
        },
        theme: {
            mode: 'light'
        },
        grid: {
            row: {
                colors: ['#cccccc', 'transparent'],
                opacity: 0.5
            }
        },
        xaxis: {
          type: 'datetime',
          categories: this.allDates
        },
        tooltip: {
              x: {
                format: 'dd/MM/yy'
              }
            },
    };
    },
    //Series and Options for newCases-newVaccinaions-compairedChart-everday
    SeriesNewCasesNewVaccinations(){
      return [{
        name: 'Neu Ansteckungen',
        data: this.historyNewCases
      },
      { name: 'Neue Erstimpfungen',
        data: this.sortedVccinated
      }]
    },
    OptionsNewCasesNewVaccinations(){
      return {
        chart: {
            type: 'area',
            zoom: {
                enabled: false
            },
            toolbar: {
                show: false
            }
        },
        title: {
              text: 'Neu Ansteckungen & Neu Impfungen',
              align: 'center',
              margin: 10,
            },
        dataLabels: {
            enabled: false,
        },
        stroke: {
            curve: 'smooth'
        },
        theme: {
            mode: 'light'
        },
        grid: {
            row: {
                colors: ['#cccccc', 'transparent'],
                opacity: 0.5
            }
        },
        xaxis: {
          type: 'datetime',
          categories: this.allDates
        },
        tooltip: {
              x: {
                format: 'dd/MM/yy'
              }
            }
      }
    }
  },
  methods: {
    async getVaccinationData(){
        const urlForVaccinations = 'https://api.corona-zahlen.org/vaccinations'
        await axios
        .get(urlForVaccinations)
            .then(response => (
          this.dataTableStates = response.data.data.vaccinated,
          this.dataTest = response.data.data.administeredVaccinations,
          this.biontechData = response.data.data.vaccination.biontech,
          this.modernaData = response.data.data.vaccination.moderna
          ))
    },
    async getVaccinationDataHistory() {
        const urlForVaccinationsHistory = 'https://api.corona-zahlen.org/vaccinations/history'
        const response2 = await axios.get(urlForVaccinationsHistory)
        
          this.historyDates = response2.data.data.history,
          this.allDates = this.historyDates.map( a => a.date),
          
          this.historyVaccinated = this.historyDates.map( v => v.vaccinated),
          this.historyVaccinated.forEach(vaccin => {
            this.sortedVccinated.push(vaccin)
          }),
          this.historyVaccinatedSeondVac = this.historyDates.map( secondV => secondV.secondVaccination),
          this.historyVaccinatedSeondVac.forEach(secVac => {
          this.sortedSecondVccinated.push(secVac)
          })
        
    },
    async getCaseDataHistory(){
      const urlForCaseHistory = 'https://api.corona-zahlen.org/germany/history/cases'
      await axios
      .get(urlForCaseHistory)
        .then(response3 => (
          this.historyCaseData = response3.data.data,
          this.historyNewCases = this.historyCaseData.map(cd => cd.cases).slice(347)
        ))
        console.log(this.historyNewCases);
    },
  },
  mounted () {
    this.getVaccinationData()
    this.getVaccinationDataHistory()
    this.getCaseDataHistory()
  },
}
</script>
